import React, { Component } from 'react';
import SearchBar from './Components/SearchView/SearchBar'
import HotelList from './Components/SearchView/HotelList'
import './App.css';
import firebase from './Components/firebase'



class App extends Component {


  reference = null;

 constructor(){
   super();
   const hotelarray = [];
   this.state = {
    hoteles: hotelarray,
    hotelesFiltrados: hotelarray
  }
  this.reference = firebase.database().ref('hoteles');
 }

 componentDidMount(){
  this.reference.on('value',(snapshot)=>{
    let hoteles = snapshot.val();
    let newHoteles = [];
    for(let hotel in hoteles){
      newHoteles.push({
        id: hotel,
        nombre: hoteles[hotel].nombre,
        descripcion: hoteles[hotel].descripcion,
        ubicacion: hoteles[hotel].ubicacion,
        ciudad: hoteles[hotel].ciudad,
        comentarios: hoteles[hotel].comentarios,
        imagen: hoteles[hotel].imagen,
        costo: hoteles[hotel].costo
      });
    }
    this.setState({
      hoteles: newHoteles
    }); 
  })
 }

 handleSearch(searchParam){
  let hotelesFiltrados = this.state.hoteles;
  hotelesFiltrados = hotelesFiltrados.onFilter((hotel)=>{
    if(!searchParam){
      return true
    }
    if(hotel.nombre.contains(searchParam)){
      return true
    }
    if(hotel.ciudad.contains(searchParam)){
      return true;
    }
  });
this.setState({
  hotelesFiltrados: this.hotelesFiltrados
})
 }
handleAdd(hotel){
  this.reference.push(hotel)  
}
 render() {
   let hoteles = this.state.hoteles
    return (
      
      <div className="App">
      <SearchBar/>
      <HotelList hoteles = {hoteles}/>
      </div>
    );
  }
}

export default App;



